body{
    width: 100vw;
    max-width: 100vw;
    padding: 0;
    margin: 0;
    min-height: 100vh;
    height: fit-content;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    background-color: #272727;
    background-color: #1d1d1d;
    user-select: none; 
   -webkit-user-select: none; 
   -khtml-user-select: none; 
   -moz-user-select: none; 
   -ms-user-select: none; 
   scroll-behavior: smooth;
}

.App{
    width: 100vw;
    height: fit-content;
    max-width: 100vw;
    position: relative;
    scroll-behavior: smooth;
}
::-webkit-scrollbar {
    width: 5px;
    height: 5px;
  }
  ::-webkit-scrollbar-track {
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
  }
  ::-webkit-scrollbar-thumb {
    background-color: #11171a;
    border-radius: 10px;
  }