@import url(https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@100;300;400;500;600;700;800;900&family=Montserrat:wght@100;300;400;500;600;700;800&family=Poppins:wght@200;300;400;500;600&family=Roboto:wght@100;300;400;500;700&family=Rubik:wght@300;400;500;600;700;800;900&display=swap);
body{
    width: 100vw;
    max-width: 100vw;
    padding: 0;
    margin: 0;
    min-height: 100vh;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    background-color: #272727;
    background-color: #1d1d1d;
    user-select: none; 
   -webkit-user-select: none; 
   -khtml-user-select: none; 
   -moz-user-select: none; 
   -ms-user-select: none; 
   scroll-behavior: smooth;
}

.App{
    width: 100vw;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    max-width: 100vw;
    position: relative;
    scroll-behavior: smooth;
}
::-webkit-scrollbar {
    width: 5px;
    height: 5px;
  }
  ::-webkit-scrollbar-track {
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
  }
  ::-webkit-scrollbar-thumb {
    background-color: #11171a;
    border-radius: 10px;
  }
.main_navbar{
    width: 10vw;
    height: 100vh;
    z-index: 1000;
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    left: 0;
    top: 0;
    background-color: #181818;
}
#portfolio{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    position: fixed;
    top: 10px;
    right: 10px;
    background-color: #fd1056;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2000000;
    border-radius: 5px;
    transition: all 200ms ease;
}
#portfolio a{
    padding: 4px;
    color: white;
    text-decoration: none;
    border: none;
    font-size: 14px;
    font-family: "Libre Franklin", sans-serif;
}
#portfolio:hover{
    opacity: 0.5;
}
#portfolio a i{
    color: black;
}
#bar{
    display: none;
    position: fixed;
    top: 10px;
    right: 10px;
    width: 40px;
    height: 40px;
    background-color: #181818;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    z-index: 2000000;
    cursor: pointer;
}
#bar span{
    width: 80%;
    color: white;
    height: 3px;
    margin: 5px 0;
    background-color: white;
    transform: rotateY(0deg) rotate(0deg);
    transition: all 300ms ease;
}
/* #bar #bar_one{
    transform: rotateY(180deg) rotate(-45deg);
} */
.main_navbar_top{
    position: absolute;
    top: 0;
    background-color: rgb(0, 0, 0);
    width: 100%;
    height: 150px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
}
.main_nav_head{
    font-family: "Libre Franklin", sans-serif;
    font-weight: 500;
    font-size: 60px;
    margin: 0;
    padding: 0;
    color: #fd1056;
    text-shadow: 0px 0px 0 #08fdd8, -1px 1px 0 #08fdd8, -2px 2px 0 #08fdd8,
    -3px 3px 0 #08fdd8, -4px 4px 0 #08fdd8;
}
.main_navbar_top h4{
    margin: 0;
    padding: 0;
    color: white;
    font-family: "Libre Franklin", sans-serif;
    font-weight: 800;
    font-size: 30px;
}


.main_navbar_mid{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 40%;
}
.main_navbar_mid a{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    text-decoration: none;
    width: 10vw;
    height: 50px;
    border-top: 1px solid #282828;
    border-bottom: 1px solid #282828;
    color: #909096;
    font-family: "Libre Franklin", sans-serif;
    font-weight: 300;
    cursor: pointer;
    transition: linear 0.4s;
    animation: none;
}
.main_navbar_mid a:hover{
    background-color: #08fdd8;
    color: black;
    animation: none;
}
.main_navbar_bottom{
    position: absolute;
    bottom: 0;
    height: 50px;
    width: 10vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.main_navbar_bottom a{
    text-decoration: none;
    color: #909096;
    font-size: 16px;
    transition: all 300ms ease;
    margin: 0 5px;
}
.main_navbar_bottom a:hover{
    color: #08fdd8;
}
@media only screen and (max-width: 1300px){
    .main_navbar{
        width: 100vw;
        max-width: 100vw;
        display: none;
    }
    .main_navbar_mid a{
        width: 100vw;
    }
    #bar{
        display: flex;
    }
    #portfolio{
        left: 10px;
    }
}   
.main_home_page {
  width: 100vw;
  min-height: 100vh;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 0;
  margin: 0;
  position: relative;
  background-color: transparent;
  left: 0vw;
  display: flex;
  flex-direction: column;
}

.front_main_page {
  width: 90vw;
  min-height: 100vh;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  position: relative;
  overflow-y: hidden;
  overflow-x: hidden;
  left: 10vw;
}
#front_lotti{
    position: absolute;
    right: 0;
    width: 50vw;
    height: 80%;
    opacity: 1;
    z-index: -1;
    animation : img 2s infinite;
}
@keyframes img{
  0%{
    margin-bottom: 50px;
}
50%{
    margin-bottom: 1px;
}
100%{
    margin-bottom: 50px;
}
}
#scroll_down{
    position: absolute;
    bottom: 5px;
    right: 30px;
    margin: 0;
    padding: 0;
    -ms-writing-mode: tb-rl;
        writing-mode: vertical-rl;
    color: #fd1056;
    color: white;
    font-family: "Libre Franklin", sans-serif;
    font-weight: 200;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}
#scroll_down i{
    margin-top: 4px;
    font-weight: 100;
}
#scroll{
    animation: ani 1s infinite;
}
@keyframes ani{
    0%{
        margin-bottom: 15px;
    }
    50%{
        margin-bottom: 1px;
    }
    100%{
        margin-bottom: 15px;
    }
}
.front_name {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: absolute;
  left: 10px;
  margin: 0;
  padding: 0;
  top: 20%;
}
.frontpage_name {
  margin: 0;
  padding: 0;
  font-size: 6vw;
  font-family: "Libre Franklin", sans-serif;
  font-weight: 600;
  color: white;

}
.text_text{
    transition: all 300ms ease;
    animation-iteration-count: 1;
}
@keyframes animation{
    0%{
        transform: translate3d(0,0,0);
        text-shadow: 0em 0em 0  #08fdd8;
    }
    30%{
        transform: translate3d(0,0,0);
        text-shadow: 0em 0em 0 #08fdd8;
    }
    70%{
        transform: translate3d(0.08em,-0.08em,0);
        text-shadow: 0.08em 0.08em #08fdd8;
    }
    100%{
        transform: translate3d(0.08em,-0.08em,0);
        text-shadow: 0.08em 0.08em #08fdd8;
    }
}
.text_text:hover{
    color: #fd1056;
    animation-name: bounce;
    animation: animation 600ms cubic-bezier(0.860, 0.000, 0.070, 1.000);
}
.frontpage_name {
  margin: 0;
  padding: 0;
}
#d {
  margin-left: 25px;
}
#text {
  margin-left: 25px;
  color: #fd1056;
  text-shadow: 0px 0px 0 #08fdd8, -1px 1px 0 #08fdd8, -2px 2px 0 #08fdd8,
    -3px 3px 0 #08fdd8, -4px 4px 0 #08fdd8;
}
.about{
    color: #8d8d8d;
    font-size: 1.1vw;
    font-family: "Libre Franklin", sans-serif;
    letter-spacing: 4px;
    margin: 0;
    padding: 0;
    font-weight: 400;
    margin-top: 4px;
}
.front_contact{
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    letter-spacing: 4px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 50px;
    width: 20vw;
    cursor: pointer;
    border: 1px solid #08fdd8;
    margin: 20px 0;
    transition: all 300ms ease;
    text-decoration: none;
    color: #08fdd8;
    font-family: "Libre Franklin", sans-serif;
    font-weight: 200;
    transition: ease-out 0.4s;
}
.front_contact:hover{
    letter-spacing: 6px;
    box-shadow: inset 400px 0 0 0 #08fdd8;
    color: black;
}

@media only screen and (max-width: 1300px) {
  .main_home_page {
    left: 0;
    width: 100vw;
  }
  .front_main_page img{
      height: 60%;
  }
  .front_main_page{
    width: 100%;
    left: 0;
  }
}
@media only screen and (max-width: 1000px) {
  .frontpage_name {
    font-size: 8vw;
  }
  #front_lotti{
    height: 50%;
}
}
@media only screen and (max-width: 700px) {
  .frontpage_name {
    font-size: 10vw;
  }
  .front_name {
    top: 35%;
  }
  .front_contact{
      width: 25vw;
      font-size: 2vw;
  }
  .about{
      font-size: 3vw;
  }
  #front_lotti{
    height: 50%;
    width : 70vw;
    opacity: 0.5;
}
}
@media only screen and (max-width: 350px){
  .front_contact{
    width: 30vw;
    font-size: 12px;
    font-weight: 500;
}
  #front_lotti{
    height: 40%;
    width : 100vw;
}
.about{
  font-size: 10px;
}
}

